import { Entypo } from "@expo/vector-icons";
import React, { useContext, useMemo } from "react";
import {
  Platform,
  StyleSheet,
  TouchableWithoutFeedback,
  View,
} from "react-native";
import { useTheme } from "react-native-elements";
import { color } from "react-native-elements/dist/config";
import { Action } from "../../backend/actions/Action";
import { BuildingActionBase } from "../../backend/buildings/Building";
import {
  changeBuildingsTurnedOn,
  getBuildingAmount,
  getBuildingAmountTurnedOn,
} from "../../backend/buildings/Buildings";
import { PlayerContext } from "../../backend/PlayerContext";
import {
  addActionToQuickbar,
  isInQuickbar,
  isQuickbarFull,
} from "../../backend/Quickbar";
import { getResourceCap, Resource } from "../../backend/Resources";
import { Button } from "./Button";

import { CostViewer } from "./CostViewer";
import { Markdown } from "./Markdown";
import { DescriptionViewer, disabledColorByTheme } from "./Miscellaneous";
import { PlusMinusAllocator } from "./PlusMinusAllocator";
import { Text } from "./Text";

export function ActionContainer(props: {
  action: Action;
  showCompact: boolean;
  showAddToQuickbar?: boolean;
  collapsed?: boolean;
  onPressExpand?: () => void;
}) {
  const playerContext = useContext(PlayerContext);
  const action = props.action;
  const showAddToQuickbar = props.showAddToQuickbar || false;
  const cost = action.getCost(playerContext);
  const isFree =
    Object.keys(cost.resources).length === 0 &&
    Object.keys(cost.items).length === 0;
  const capIsEnough = (Object.keys(cost.resources) as Array<Resource>).every(
    (resource: Resource) => {
      return (
        (cost.resources[resource] ?? 0) <=
        getResourceCap(playerContext, resource)
      );
    },
  );
  const canTurnOff =
    action instanceof BuildingActionBase && action.canTurnOff();
  const buildingAmount =
    action instanceof BuildingActionBase &&
    getBuildingAmount(playerContext, action);
  const buildingAmountOn =
    action instanceof BuildingActionBase &&
    getBuildingAmountTurnedOn(playerContext, action);

  const descriptionContainer = useMemo(() => {
    const descriptionContents = `${action.getDisplayDescription(
      playerContext,
    )} **${action.getDisplayEffect(playerContext)}**`;
    return (
      <View style={styles.descriptionTextContainer}>
        <DescriptionViewer style={{ flex: 1 }}>
          {descriptionContents}
        </DescriptionViewer>
      </View>
    );
  }, [
    !props.collapsed && action.getDisplayDescription(playerContext),
    !props.collapsed && action.getDisplayEffect(playerContext),
  ]);

  const costContainer = useMemo(() => {
    return (
      <View style={styles.costContainer}>
        <Markdown>Cost:</Markdown>
        <CostViewer
          resources={action.getCost(playerContext).resources}
          items={action.getCost(playerContext).items}
        />
      </View>
    );
  }, [!props.collapsed && JSON.stringify(action.getCost(playerContext))]);

  const turnOffContainer = useMemo(() => {
    if (!canTurnOff) {
      return null;
    }
    const amountActiveText = `${buildingAmountOn}/${buildingAmount}`;
    return (
      <View style={styles.turnOnRow}>
        <Markdown>Amount active:</Markdown>
        <View style={styles.turnOnQuantityContainer}>
          <Markdown>{amountActiveText}</Markdown>
          <View style={styles.plusMinusContainer}>
            <PlusMinusAllocator
              disableMinus={buildingAmountOn === 0}
              disablePlus={buildingAmountOn === buildingAmount}
              onPressMinus={() =>
                playerContext.apply(
                  changeBuildingsTurnedOn(action as BuildingActionBase, -1),
                )
              }
              onPressPlus={() =>
                playerContext.apply(
                  changeBuildingsTurnedOn(action as BuildingActionBase, 1),
                )
              }
            />
          </View>
        </View>
      </View>
    );
  }, [canTurnOff, buildingAmount, buildingAmountOn]);
  const theme = useTheme();

  const disabled = !action.isEnabled(playerContext);

  return useMemo(() => {
    const capTooLowColorBuilder = color(disabledColorByTheme(theme.theme))
      .hsl()
      .hue(0);
    const capTooLowColor =
      theme.theme.mode === "dark"
        ? capTooLowColorBuilder.saturationl(32).lighten(0.3).string()
        : capTooLowColorBuilder.saturationl(48).lighten(0.05).string();
    const capTooLowBackgroundBuilder = color(theme.theme.colors.disabled)
      .hsl()
      .hue(0);
    const capTooLowBackground =
      theme.theme.mode === "dark"
        ? capTooLowBackgroundBuilder.darken(0.2).string()
        : capTooLowBackgroundBuilder.darken(0.1).string();
    const button = (
      <Button
        key={action.getName()}
        onPress={() =>
          playerContext.apply(
            action.doAction.bind(action, { isAutomatic: false }),
          )
        }
        enableAutoRepress={true}
        title={action.getDisplayName(playerContext)}
        titleStyle={styles.buttonTitle}
        containerStyle={[styles.buttonContainer]}
        disabledStyle={!capIsEnough && { backgroundColor: capTooLowBackground }}
        disabledTitleStyle={!capIsEnough && { color: capTooLowColor }}
        disabled={disabled}
      />
    );
    const addToQuickbarButton = (
      <Button
        onPress={() =>
          playerContext.apply((state) => addActionToQuickbar(state, action))
        }
        title="Add to Quickbar"
        titleStyle={styles.smallButtonTitle}
        containerStyle={[styles.quickbarButtonContainer]}
        disabled={
          isInQuickbar(playerContext, {
            type: "action",
            name: action.getName(),
          }) || isQuickbarFull(playerContext)
        }
      />
    );
    return (
      <View style={styles.actionContainer} key={action.getName()}>
        <View style={styles.buttonRowContainer}>
          <View style={styles.buttonContainerView}>
            {props.showCompact && props.collapsed && (
              <Text
                h3
                key={action.getName()}
                onPress={props.onPressExpand}
                style={[
                  styles.buttonContainer,
                  disabled && { color: disabledColorByTheme(theme.theme) },
                  !capIsEnough && { color: capTooLowColor },
                ]}
              >
                {action.getDisplayName(playerContext)}
              </Text>
            )}
            {(!props.collapsed || !props.showCompact) && button}
            {showAddToQuickbar && addToQuickbarButton}
          </View>
          <TouchableWithoutFeedback onPress={props.onPressExpand}>
            <View style={styles.expandContainer}>
              <Entypo
                name={props.collapsed ? "chevron-right" : "chevron-down"}
                size={24}
                color={theme.theme.colors.black}
              />
            </View>
          </TouchableWithoutFeedback>
        </View>
        {!props.collapsed && (
          <View>
            {descriptionContainer}
            {!isFree && costContainer}
            {canTurnOff && turnOffContainer}
          </View>
        )}
      </View>
    );
  }, [
    action.getName(),
    action.getDisplayName(playerContext),
    disabled,
    canTurnOff,
    turnOffContainer,
    !props.collapsed && costContainer,
    !props.collapsed && descriptionContainer,
    capIsEnough,
    showAddToQuickbar,
    showAddToQuickbar && JSON.stringify(playerContext.quickbar),
    props.collapsed,
    props.onPressExpand,
    theme,
  ]);
}

const styles = StyleSheet.create({
  actionContainer: { marginVertical: 8 },
  buttonRowContainer: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
  quickbarButtonContainer: {
    marginLeft: 8,
  },
  expandContainer: {
    width: 64,
    flexDirection: "row",
    justifyContent: "center",
    alignSelf: "stretch",
    alignItems: "center",
    cursor: "pointer",
  },
  costContainer: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "flex-start",
    marginTop: 4,
  },
  descriptionTextContainer: {
    marginTop: 8,
    flex: 1,
    flexDirection: "row",
    flexWrap: "wrap",
  },
  buttonTitle: {
    fontSize: 16,
    textAlign: "center",
    paddingVertical: 1,
    ...Platform.select({
      android: {
        fontFamily: "sans-serif-medium",
      },
      default: {
        fontSize: 18,
      },
    }),
  },
  smallButtonTitle: {
    fontSize: 12,
  },
  buttonContainerView: {
    flexDirection: "row",
    flex: 1,
  },
  buttonContainer: {
    flex: 1,
  },
  buttonContainerSmall: {
    marginRight: 0,
  },
  capTooLowText: {
    color: "#d99",
  },
  turnOnRow: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    marginTop: 8,
  },
  turnOnQuantityContainer: {
    flexDirection: "row",
    alignItems: "center",
  },
  plusMinusContainer: {
    marginLeft: 8,
  },
  spellButtonTitleContainer: {
    flex: 1,
    flexDirection: "row",
  },
  spellButtonTitleOnlyContainer: {
    flex: 1,
  },
  spellButtonCostContainer: {
    flexDirection: "row",
    width: 40,
    justifyContent: "flex-end",
  },
  spellManaIcon: {
    height: 16,
    width: 16,
    justifyContent: "flex-end",
    alignItems: "center",
    top: 1,
    marginLeft: 2,
  },
});
