import React from "react";
import { StyleSheet, View } from "react-native";
import { useTheme } from "react-native-elements";
import { ItemTag } from "../../backend/items/Item";
import { Text } from "../utility/Text";

export function ItemTags(props: { tags: ItemTag[] }) {
  const theme = useTheme();
  return (
    <View style={styles.tagRow}>
      {props.tags.map((tag) => (
        <View
          style={[
            styles.tagStyle,
            {
              backgroundColor: theme.theme.colors.grey0,
              borderColor: theme.theme.colors.grey0,
            },
          ]}
          key={tag.tag}
        >
          <Text
            style={[styles.tagTextStyle, { color: theme.theme.colors.white }]}
          >
            {tag.tag}
          </Text>
        </View>
      ))}
    </View>
  );
}

const styles = StyleSheet.create({
  tagRow: {
    flexDirection: "row",
    flexWrap: "wrap",
  },
  tagStyle: {
    textAlign: "center",
    paddingHorizontal: 6,
    borderRadius: 8,
    height: 18,
    marginRight: 4,
  },
  tagTextStyle: {
    fontSize: 11,
    lineHeight: 16,
  },
});
