import { Picker as RNPicker } from "@react-native-picker/picker";
import { StyleProp, TextStyle } from "react-native";
import { useTheme } from "react-native-elements";

export function Picker(props: {
  selectedValue: string;
  onValueChange: (itemValue: string, itemIndex: number) => void;
  data: { key: string; label: string; value: string }[];
  style?: StyleProp<TextStyle>;
}) {
  const { style, ...rest } = props;
  const theme = useTheme();
  return (
    <RNPicker
      {...rest}
      style={[
        {
          color: theme.theme.colors.black,
          backgroundColor: theme.theme.colors.grey5,
        },
        style,
      ]}
      dropdownIconColor={theme.theme.colors.black}
      dropdownIconRippleColor={theme.theme.colors.black}
    >
      {props.data.map((data) => (
        <RNPicker.Item label={data.label} value={data.value} key={data.key} />
      ))}
    </RNPicker>
  );
}
