import { LinearGradient } from "expo-linear-gradient";
import React from "react";
import { ImageSourcePropType, StyleSheet, View } from "react-native";
import { Image, useTheme } from "react-native-elements";
import { sanitizeImageSourceForElectron } from "../utility/icons/IconUtils";

export function EnemySpriteTile(props: {
  sprite?: ImageSourcePropType;
  backdropColor?: string | false;
}) {
  const LinearGradientComponent = LinearGradient;
  const theme = useTheme();
  let sprite = sanitizeImageSourceForElectron(props.sprite);
  return (
    <View style={[styles.tile, { borderColor: theme.theme.colors.grey5 }]}>
      {props.backdropColor && (
        <LinearGradientComponent
          colors={[`${props.backdropColor}`, `${props.backdropColor}0`]}
          style={styles.gradient}
        />
      )}
      {sprite && (
        <Image
          resizeMode="contain"
          source={sprite}
          style={styles.image}
          placeholderStyle={{ backgroundColor: theme.theme.colors.background }}
        />
      )}
    </View>
  );
}

const styles = StyleSheet.create({
  tile: {
    borderWidth: 1,
    height: 144,
    width: 144,
    justifyContent: "center",
    alignItems: "stretch",
    margin: 8,
  },
  image: {
    height: 144,
    width: 144,
  },
  gradient: {
    height: 144,
    width: 144,
    position: "absolute",
    left: 0,
    top: 0,
    opacity: 0.2,
  },
});
