import React, { useMemo } from "react";
import { StyleSheet, View } from "react-native";
import { Button } from "./Button";

type PlusMinusAllocatorProps = {
  disableMinus: boolean;
  disablePlus: boolean;
  onPressMinus: () => void;
  onPressPlus: () => void;
};

export function PlusMinusAllocator(
  props: PlusMinusAllocatorProps = {
    disableMinus: false,
    disablePlus: false,
    onPressMinus: () => {},
    onPressPlus: () => {},
  },
) {
  const minusButton = useMemo(
    () => (
      <Button
        enableAutoRepress={true}
        containerStyle={styles.plusMinusAllocatorButton}
        style={styles.plusMinusAllocatorButtonInner}
        iconContainerStyle={styles.plusMinusAllocatorButtonInner}
        buttonStyle={styles.plusMinusAllocatorButtonInner}
        title="-"
        onPress={props.onPressMinus}
        disabled={props.disableMinus}
        autoRepressConfig={{ minTimeMs: 50 }}
      />
    ),
    [props.onPressMinus, props.disableMinus],
  );
  const plusButton = useMemo(
    () => (
      <Button
        enableAutoRepress={true}
        containerStyle={styles.plusMinusAllocatorButton}
        style={styles.plusMinusAllocatorButtonInner}
        iconContainerStyle={styles.plusMinusAllocatorButtonInner}
        buttonStyle={styles.plusMinusAllocatorButtonInner}
        title="+"
        onPress={props.onPressPlus}
        disabled={props.disablePlus}
        autoRepressConfig={{ minTimeMs: 50 }}
      />
    ),
    [props.onPressPlus, props.disablePlus],
  );
  return useMemo(
    () => (
      <View style={styles.row}>
        {minusButton}
        {plusButton}
      </View>
    ),
    [minusButton, plusButton],
  );
}

const styles = StyleSheet.create({
  plusMinusAllocatorButton: {
    height: 32,
    width: 32,
    borderRadius: 16,
    marginHorizontal: 4,
  },
  plusMinusAllocatorButtonInner: {
    paddingHorizontal: 0,
    paddingVertical: 0,
    height: 32,
  },
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
});
