import React, { useContext, useState } from "react";
import { StyleSheet, View } from "react-native";
import { Button, useTheme } from "react-native-elements";
import { PlayerContext } from "../backend/PlayerContext";
import { retire } from "../backend/Retire";
import {
  maximumSchoolLevelProductionMultiplierDx,
  maximumSchoolLevelResearchMultiplier,
} from "../backend/spells/ElementsAndSchools";
import { Storylines } from "../backend/storylines/Storylines";
import { clone } from "../utils/CoreUtils";
import { formatNumber } from "../utils/FormattingUtils";
import { Markdown } from "./utility/Markdown";
import { Text } from "./utility/Text";

import UseTimePiecesResearch from "../backend/actions/campus/UseTimePiecesResearch";
import { calculateMemoryChamberExplorationMultiplier } from "../backend/events/storyline/MemoryChamber";
import { hasGlobalFlag, hasGlobalFlagIncludingNextRun } from "../backend/Flags";

export default function Retirement() {
  const playerContext = useContext(PlayerContext);

  const modifiedContext = retire(clone(playerContext), true);
  const newResearchMultiplier =
    maximumSchoolLevelResearchMultiplier(modifiedContext);
  const newProductionMultiplier =
    maximumSchoolLevelProductionMultiplierDx(modifiedContext);
  const oldResearchMultiplier =
    maximumSchoolLevelResearchMultiplier(playerContext);
  const oldProductionMultiplier =
    maximumSchoolLevelProductionMultiplierDx(playerContext);
  const researchDeltaPct =
    ((newResearchMultiplier - oldResearchMultiplier) * 100.0) /
    oldResearchMultiplier;
  const productionDeltaPct =
    ((newProductionMultiplier - oldProductionMultiplier) * 100.0) /
    oldProductionMultiplier;

  const newStorylines = Storylines.getAll().filter(
    (storyline) =>
      !storyline.isBonusActive(playerContext) &&
      storyline.isCompleted(playerContext),
  );

  let memoryChamberMessage = "";
  const completedMemoryChamber = hasGlobalFlag(
    playerContext,
    "memory_chamber_completed",
  );
  const completedUpgradedMemoryChamber = hasGlobalFlagIncludingNextRun(
    playerContext,
    "upgraded_memory_chamber_completed",
  );
  if (completedMemoryChamber && !completedUpgradedMemoryChamber) {
    const memoryChamberMultiplierBefore =
      calculateMemoryChamberExplorationMultiplier(playerContext);
    const memoryChamberMultiplierAfter =
      calculateMemoryChamberExplorationMultiplier(modifiedContext);
    const memoryChamberMultiplierDeltaPct =
      ((memoryChamberMultiplierAfter - memoryChamberMultiplierBefore) * 100.0) /
      memoryChamberMultiplierBefore;
    memoryChamberMessage = ` Your Memory Chamber will also reduce the required exploration for previously completed areas to **${formatNumber(
      memoryChamberMultiplierAfter * 100,
      { showDecimals: false },
    )}% (${
      memoryChamberMultiplierDeltaPct != 0
        ? formatNumber(memoryChamberMultiplierDeltaPct, {
            showDecimals: false,
          }) + "%"
        : "no change"
    })**.`;
  }

  let storylineBonusMessage = "";
  if (newStorylines.length > 0) {
    storylineBonusMessage = ` The following Storyline bonuses will also fully activate:
    
${newStorylines
  .map(
    (storyline) =>
      `* **${storyline.name}**: ${storyline.getBonusDescription(
        playerContext,
      )}`,
  )
  .join("\n")}`;
  }

  const retirementEffects = `**If you retire now, your research multiplier will become ${formatNumber(
    newResearchMultiplier,
    { showDecimals: true },
  )}x (+${formatNumber(researchDeltaPct, {
    showDecimals: true,
  })}%) and your production and loot multiplier will become ${formatNumber(
    newProductionMultiplier,
    { showDecimals: true },
  )}x (+${formatNumber(productionDeltaPct, {
    showDecimals: true,
  })}%).**${memoryChamberMessage}${storylineBonusMessage}`;

  const text = `You have built up enough knowledge that you feel it might be enough. You consider retirement.

If you retire, **you will reset the game.** Everything will revert back to its initial state, except:
* Any unlocked schools or elements will stay unlocked;
* Any bonuses you obtained from "(Storyline)" events will remain;
* You will gain a boost to research, building production and loot based on the maximum level you have ever attained on your primary school (MPL), as well as on the sum of the MPLs you have attained on each school you have unlocked.

When you retire, you will be able to choose a new primary school and element.

${retirementEffects}

*Do you want to retire?*`;

  const [hasClicked, setHasClicked] = useState(false);
  const showWarning =
    UseTimePiecesResearch.isVisible(playerContext) &&
    UseTimePiecesResearch.isEnabled(playerContext);
  const theme = useTheme();

  return (
    <View style={styles.container}>
      <Text h1>Retirement</Text>
      <View style={styles.explanation}>
        <Markdown>{text}</Markdown>
      </View>
      {showWarning && (
        <View style={styles.warning}>
          <Text style={{ color: theme.theme.colors.warning }}>
            Warning: it looks like you still have many unused Time Pieces!
            Consider using them up with "Use Time Pieces (Research)" (or in
            other ways) before retiring, as they will be gone when you retire!
          </Text>
        </View>
      )}
      <View style={styles.buttonContainer}>
        {!hasClicked && (
          <Button title="Retire" onPress={() => setHasClicked(true)} />
        )}
        {hasClicked && (
          <Button
            title="Are you sure? Press again to confirm"
            onPress={() => playerContext.apply((state) => retire(state, false))}
          />
        )}
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
  explanation: {
    marginTop: 16,
  },
  buttonContainer: {
    marginTop: 16,
  },
  warning: {
    marginTop: 8,
  },
});
