import { SimpleLineIcons } from "@expo/vector-icons";
import React, {
  useCallback,
  useContext,
  useMemo,
  useRef,
  useState,
} from "react";
import { Platform, StyleSheet, View } from "react-native";
import { Button, useTheme } from "react-native-elements";
import { hasGlobalFlag, setGlobalFlag } from "../../backend/Flags";
import { PlayerContext, PlayerContextValue } from "../../backend/PlayerContext";
import { School } from "../../backend/spells/ElementsAndSchools";
import { Spell } from "../../backend/spells/Spell";
import { getVisibleSpellsBySchool } from "../../backend/spells/Spells";
import CategoryContainer from "../utility/CategoryContainer";
import { Picker as UtilityPicker } from "../utility/Picker";
import { SpellView } from "../utility/SpellView";
import { Text } from "../utility/Text";
import {
  UIContext,
  UIContextValue,
  useUIStateFromContext,
} from "../utility/UIContext";
import { FavoriteSpellsContainer } from "./FavoriteSpellsContainer";

function SpellsContainerInside(props: {
  uiKey: string;
  area?: string;
  onlyBySchool?: boolean;
  showAddToQuickbar?: boolean;
  playerContext: PlayerContextValue;
  uiContext: UIContextValue;
  isVisible?: boolean;
}) {
  const playerContext = props.playerContext;
  const playerContextRef = useRef(playerContext);
  const uiContext = props.uiContext;

  const currentSchoolKey = props.uiKey + ".currentSchool";
  let spellsBySchool = getVisibleSpellsBySchool(playerContext);
  for (let school in spellsBySchool) {
    spellsBySchool[school] = spellsBySchool[school].filter(
      (spell) => !props.area || spell.getArea() == props.area,
    );
    if (spellsBySchool[school].length == 0) {
      delete spellsBySchool[school];
    }
  }

  const [currentSchool, setCurrentSchool] = useUIStateFromContext(
    uiContext,
    currentSchoolKey,
    Object.keys(spellsBySchool)?.[0] ?? "Conjuration",
  );

  const [expandedSpells, setExpandedSpells] = useState<Record<string, boolean>>(
    {},
  );
  const expandedSpellsRef = useRef(expandedSpells);
  expandedSpellsRef.current = expandedSpells;

  const setCurrentSchoolCallback = useCallback(
    (school: string) => {
      setCurrentSchool(school);
      if (
        !hasGlobalFlag(
          playerContextRef.current,
          "has_completed_spell_menu_picker_nux",
        )
      ) {
        playerContextRef.current.apply(
          setGlobalFlag("has_completed_spell_menu_picker_nux"),
        );
      }
    },
    [setCurrentSchool],
  );

  const fullSpellString = Object.keys(spellsBySchool)
    .map((school) =>
      Object.values(spellsBySchool[school])
        .map((spell) => spell.getName())
        .join(","),
    )
    .join(";");
  const allSpellString = (spellsBySchool?.[currentSchool as School] || [])
    .map((spell) => spell.getName())
    .join(",");

  const currentSchoolSpells = useMemo(
    () => spellsBySchool?.[currentSchool as School] || [],
    [allSpellString],
  );

  const toggleCollapsed = useCallback((spell: Spell) => {
    const spellName = spell.getName();
    expandedSpellsRef.current[spellName] =
      !expandedSpellsRef?.current?.[spellName];
    setExpandedSpells(expandedSpellsRef.current);
  }, []);

  const boundToggleCollapsed = useMemo(
    () =>
      Object.fromEntries(
        Object.keys(spellsBySchool).map((school) => [
          school,
          Object.fromEntries(
            spellsBySchool[school].map((spell) => [
              spell.getName(),
              toggleCollapsed.bind(null, spell),
            ]),
          ),
        ]),
      ),
    [fullSpellString],
  );

  const spellActionContainers = useMemo(() => {
    if (!props.onlyBySchool) {
      return Object.keys(spellsBySchool).map((school) => {
        const spellArray = spellsBySchool[school];
        return (
          <View
            key={school}
            style={currentSchool !== school && { display: "none" }}
          >
            {spellArray.map((spell, idx) => {
              return (
                <SpellView
                  key={spell.getSpellName()}
                  spell={spell}
                  showAddToQuickbar={props.showAddToQuickbar}
                  collapsed={!expandedSpells?.[spell.getSpellName()]}
                  toggleCollapsed={
                    boundToggleCollapsed[school][spell.getName()]
                  }
                  isVisible={props.isVisible && currentSchool === school}
                />
              );
            })}
          </View>
        );
      });
    }
    return currentSchoolSpells.map((spell, idx) => {
      return (
        <SpellView
          key={spell.getSpellName()}
          spell={spell}
          showAddToQuickbar={props.showAddToQuickbar}
          collapsed={!expandedSpells?.[spell.getSpellName()]}
          toggleCollapsed={boundToggleCollapsed[currentSchool][spell.getName()]}
          isVisible={props.isVisible}
        />
      );
    });
  }, [
    !props.onlyBySchool ? fullSpellString : currentSchoolSpells,
    JSON.stringify(expandedSpells),
    boundToggleCollapsed,
    currentSchool,
    props.isVisible,
  ]);

  const tags = Object.keys(spellsBySchool);

  const spellSchoolPicker = useMemo(
    () => (
      <UtilityPicker
        onValueChange={setCurrentSchoolCallback}
        style={styles.picker}
        selectedValue={currentSchool}
        data={tags.map((tag, idx) => ({ label: tag, value: tag, key: tag }))}
      />
    ),
    [setCurrentSchoolCallback, currentSchool, JSON.stringify(tags)],
  );

  const expandContractAll = useCallback(() => {
    let expandedSomething = false;
    currentSchoolSpells.forEach((spell) => {
      const spellName = spell.getName();
      if (!expandedSpells?.[spellName]) {
        expandedSomething = true;
        expandedSpells[spellName] = true;
      }
    });
    if (!expandedSomething) {
      currentSchoolSpells.forEach((spell) => {
        delete expandedSpells[spell.getName()];
      });
    }
    setExpandedSpells(expandedSpells);
  }, [
    expandedSpells,
    currentSchoolSpells.map((spell) => spell.getName()).join(","),
  ]);

  const theme = useTheme();

  const showNUX = !hasGlobalFlag(
    playerContext,
    "has_completed_spell_menu_picker_nux",
  );

  return useMemo(() => {
    return (
      <View>
        <View style={styles.emptyTitleContainer}>
          <SimpleLineIcons
            name="magic-wand"
            size={24}
            style={styles.magicWand}
            color={theme.theme.colors.black}
          />
          <Text h2>Cast Spell</Text>
        </View>
        {!props.onlyBySchool && (
          <View>
            <FavoriteSpellsContainer isVisible={props.isVisible} />
          </View>
        )}
        {Object.keys(spellsBySchool).length > 0 ? (
          <CategoryContainer title="By School">
            <View style={styles.schoolRowContainer}>
              <View
                style={[
                  styles.tagPickerContainer,
                  Platform.select({
                    android: { backgroundColor: "#eee", borderRadius: 10 },
                    default: null,
                  }),
                ]}
              >
                {spellSchoolPicker}
              </View>
              <Button
                titleStyle={styles.buttonTitle}
                title="Expand all"
                onPress={expandContractAll}
              />
            </View>
            {showNUX && (
              <Text style={styles.nuxText}>
                Tip: You can find more spells by{" "}
                {Platform.OS == "ios"
                  ? "pressing the " + currentSchool + " button"
                  : "changing the dropdown"}{" "}
                above to switch to a different school!
              </Text>
            )}
            <View style={styles.schoolContainer}>{spellActionContainers}</View>
          </CategoryContainer>
        ) : (
          <View style={styles.noSpellsContainer}>
            <Text style={styles.noSpellsText}>No spells yet!</Text>
          </View>
        )}
      </View>
    );
  }, [
    currentSchool,
    setCurrentSchool,
    spellActionContainers,
    props.isVisible,
    spellSchoolPicker,
    expandContractAll,
    props.onlyBySchool,
    theme,
  ]);
}

export default function SpellsContainer(props: {
  uiKey: string;
  area?: string;
  hideRecent?: boolean;
  showAddToQuickbar?: boolean;
  isVisible?: boolean;
  onlyBySchool?: boolean;
}) {
  const playerContext = useContext(PlayerContext);
  const uiContext = useContext(UIContext);

  return useMemo(
    () => (
      <SpellsContainerInside
        playerContext={playerContext}
        uiContext={uiContext}
        {...props}
      />
    ),
    [
      props.area,
      props.hideRecent,
      props.showAddToQuickbar,
      props.onlyBySchool,
      props.isVisible !== false ? playerContext : false,
      props.isVisible !== false ? uiContext : false,
    ],
  );
}

const styles = StyleSheet.create({
  schoolContainer: { marginTop: 8 },
  spellsContainer: { marginTop: 8 },
  schoolButton: { marginVertical: 4 },
  magicWand: { marginRight: 8 },
  schoolTitleContainer: {
    flexDirection: "row",
    alignItems: "center",
    flex: 1,
    justifyContent: "center",
  },
  emptyTitleContainer: {
    flexDirection: "row",
    justifyContent: "center",
  },
  titleContainer: {
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
  },
  schoolTitleTextContainer: {},
  picker: {
    flex: 1,
  },
  schoolRowContainer: {
    marginTop: 8,
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
  },
  tagPickerContainer: {
    width: Platform.select({ default: 190, web: 180 }),
  },
  buttonTitle: {
    textAlign: "center",
    paddingVertical: 1,
    ...Platform.select({
      android: {
        fontFamily: "sans-serif-medium",
      },
      default: {
        fontSize: 18,
      },
    }),
    fontSize: 12,
  },
  noSpellsText: {
    fontStyle: "italic",
  },
  noSpellsContainer: {
    marginTop: 16,
  },
  nuxText: {
    fontStyle: "italic",
  },
});
