import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import { Platform, StyleSheet, View } from "react-native";
import { CheckBox } from "react-native-elements";
import { getAllItemsEverCraftable } from "../../backend/items/Items";
import { PlayerContext } from "../../backend/PlayerContext";
import {
  addContextListener,
  clearContextListener,
} from "../../backend/PlayerContextListeners";
import { Picker as UtilityPicker } from "../utility/Picker";
import { UIContext, useUIStateFromContext } from "../utility/UIContext";
import { CraftingDetails } from "./CraftingDetails";
import { ItemGrid } from "./ItemGrid";

export default function Crafting() {
  const playerContext = useContext(PlayerContext);
  const uiContext = useContext(UIContext);
  const [selectedIndex, setSelectedIndex] = useState(null as number | null);
  const [selectedTag, setSelectedTag] = useUIStateFromContext(
    uiContext,
    "CraftingTag",
    "All",
  );
  const maxSchoolLevels = playerContext.global.maxSchoolLevels;
  let craftableItems = useMemo(() => {
    const allItems = getAllItemsEverCraftable(playerContext);
    return allItems;
  }, [JSON.stringify(maxSchoolLevels)]);

  const tagSet = new Set<string>();
  craftableItems.forEach((item) => {
    const tags = item.getTags(playerContext, item.getDefaultParams());
    tags.forEach((tag) => tagSet.add(tag.tag));
  });
  const tags = Array.from(tagSet);

  useEffect(() => setSelectedIndex(null), [selectedTag]);

  const selectedIndexRef = useRef(selectedIndex);
  selectedIndexRef.current = selectedIndex;

  useEffect(() => {
    addContextListener("CraftingComponent", (oldState, newState) => {
      const oldCraftable = getAllItemsEverCraftable(oldState);
      const newCraftable = getAllItemsEverCraftable(newState);
      const oldSelectedItem =
        selectedIndexRef.current != null
          ? oldCraftable[selectedIndexRef.current]
          : undefined;
      const newSelectedItem =
        selectedIndexRef.current != null
          ? newCraftable[selectedIndexRef.current]
          : undefined;

      if (
        newSelectedItem === undefined ||
        oldSelectedItem?.getId() !== newSelectedItem?.getId()
      ) {
        setSelectedIndex(null);
      }
    });
    return () => clearContextListener("CraftingComponent");
  }, []);

  const onPressItem = React.useCallback(
    (item: any, index: number) => {
      setSelectedIndex(index);
    },
    [setSelectedIndex],
  );

  const tagButtons = useMemo(() => {
    return (
      <UtilityPicker
        onValueChange={setSelectedTag}
        style={styles.picker}
        selectedValue={selectedTag}
        data={["All", ...tags].map((tag) => ({
          label: tag,
          value: tag,
          key: tag,
        }))}
      />
    );
  }, [setSelectedTag, selectedTag, JSON.stringify(tags)]);

  if (selectedTag !== "All") {
    craftableItems = craftableItems
      .filter((item) =>
        item
          .getTags(playerContext, item.getDefaultParams())
          .find((tag) => tag.tag == selectedTag),
      )
      .sort((a, b) => {
        const maxA = Math.max(
          ...(Object.values(
            a.getCraftingSchoolLevelRequirements(playerContext),
          ).filter((n) => n != undefined) as number[]),
        );
        const maxB = Math.max(
          ...(Object.values(
            b.getCraftingSchoolLevelRequirements(playerContext),
          ).filter((n) => n != undefined) as number[]),
        );
        return maxA - maxB;
      });
  }

  const selectedItem =
    selectedIndex != null ? craftableItems[selectedIndex] : undefined;

  const [hideLocked, setHideLocked] = useUIStateFromContext(
    uiContext,
    "crafting_hide_locked",
    false,
  );

  const toggleLockedCallback = useCallback(() => {
    setHideLocked(!hideLocked);
  }, [hideLocked, setHideLocked]);

  useEffect(() => setSelectedIndex(null), [hideLocked]);

  const itemGridElements = craftableItems.map((item) => ({
    itemId: item.getId(),
    amount: 1,
    disabled: !item.isCraftable(playerContext),
  }));

  const itemGridElementsJSON = JSON.stringify(itemGridElements);

  const isAnyLocked = useMemo(() => {
    return itemGridElements.some((element) => element.disabled);
  }, [itemGridElementsJSON]);

  const itemGridElementsFiltered = useMemo(() => {
    if (hideLocked) {
      return itemGridElements.filter(
        (itemGridElement) => !itemGridElement.disabled,
      );
    }
    return itemGridElements;
  }, [itemGridElementsJSON, hideLocked]);

  const itemGridElementsFilteredJSON = JSON.stringify(itemGridElementsFiltered);

  const itemGrid = useMemo(
    () => (
      <ItemGrid items={itemGridElementsFiltered} onPressItem={onPressItem} />
    ),
    [itemGridElementsFilteredJSON, onPressItem],
  );

  const details = useMemo(
    () => (
      <View style={styles.detailsContainer}>
        {selectedItem && <CraftingDetails item={selectedItem} />}
      </View>
    ),
    [selectedItem?.getId()],
  );

  return (
    <View style={styles.container}>
      <View style={styles.row}>
        <View style={styles.tagPickerContainer}>{tagButtons}</View>
        {isAnyLocked && (
          <CheckBox
            title="Hide locked"
            checked={hideLocked}
            onPress={toggleLockedCallback}
            textStyle={styles.checkboxText}
            containerStyle={styles.checkboxContainer}
          />
        )}
      </View>
      {itemGrid}
      {details}
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
  detailsContainer: {
    marginTop: 16,
    paddingHorizontal: 8,
    paddingBottom: 8,
  },
  buttonGroupContainer: {
    flexDirection: "row",
    alignItems: "flex-start",
    height: undefined,
    flexWrap: "wrap",
    justifyContent: "flex-start",
    borderWidth: 0,
  },
  buttonGroupButtonContainer: {
    flex: 1,
    height: 40,
    borderWidth: 1,
    borderColor: "#eee",
    margin: 4,
  },
  picker: {
    flex: 1,
  },
  tagPickerContainer: {
    width: 180,
    marginHorizontal: 8,
    ...Platform.select({
      android: { backgroundColor: "#eee", borderRadius: 10, default: {} },
    }),
  },
  row: {
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
    flexWrap: "wrap",
    marginBottom: 16,
  },
  checkboxContainer: {
    marginLeft: 0,
    padding: 0,
  },
  checkboxText: {
    fontWeight: "normal",
  },
});
