import { registerGameEvent } from "./Events";
import { EventMessage, EventTag, EventTransform, GameEvent } from "./GameEvent";

class SimpleGameEvent implements GameEvent {
  constructor(
    private id: string,
    private message: EventMessage,
    private tags: EventTag[],
    private onTriggerTransform?: EventTransform,
  ) {
    registerGameEvent(this);
  }

  onTrigger(): EventTransform | undefined {
    return this.onTriggerTransform;
  }

  getId(): string {
    return this.id;
  }

  getMainEventMessage(): EventMessage {
    return this.message;
  }

  getTags(): EventTag[] {
    return this.tags;
  }
}

export function createGameEvent(
  id: string,
  message: EventMessage,
  tags: EventTag[],
  onTrigger?: EventTransform,
): GameEvent {
  return new SimpleGameEvent(id, message, tags, onTrigger);
}
