import React, { useEffect, useMemo, useRef } from "react";
import {
  Animated,
  Platform,
  ScrollView,
  StyleSheet,
  TouchableWithoutFeedback,
} from "react-native";
import { useTheme } from "react-native-elements";
import {
  Directions,
  Gesture,
  GestureDetector,
} from "react-native-gesture-handler";
import SpellsContainer from "./SpellsContainer";

type Props = {
  visible: boolean;
  onBackgroundPress: () => void;
  onSwipeLeft: () => void;
  onSwipeRight: () => void;
};

export function SpellMenuContents(props: {
  onSwipeLeft: () => void;
  onSwipeRight: () => void;
  visible: boolean;
}) {
  const rightGesture = useMemo(
    () =>
      Gesture.Fling()
        .direction(Directions.RIGHT)
        .onEnd((e: any, success) => {
          if (success && (Platform.OS != "web" || e.translationX > 0)) {
            props.onSwipeRight();
          }
        }),
    [props.onSwipeRight],
  );
  const leftGesture = useMemo(
    () =>
      Gesture.Fling()
        .direction(Directions.LEFT)
        .onEnd((e: any, success) => {
          if (success && (Platform.OS != "web" || e.translationX < 0)) {
            props.onSwipeLeft();
          }
        }),
    [props.onSwipeLeft],
  );
  const nativeGesture = useMemo(() => Gesture.Native(), []);
  const compositeGesture = useMemo(
    () => Gesture.Simultaneous(rightGesture, leftGesture, nativeGesture),
    [rightGesture, leftGesture, nativeGesture],
  );
  return (
    <GestureDetector key="spellMenuContentsGesture" gesture={compositeGesture}>
      <ScrollView contentContainerStyle={styles.magicContainer}>
        <SpellsContainer isVisible={props.visible} uiKey="SpellMenuContainer" />
      </ScrollView>
    </GestureDetector>
  );
}

export function SpellMenu(props: Props) {
  const positionAnim = useRef(
    new Animated.Value(props.visible ? 0 : 300),
  ).current;

  const opacityAnim = useRef(
    new Animated.Value(props.visible ? 0.5 : 0),
  ).current;

  const theme = useTheme();

  useEffect(() => {
    if (props.visible) {
      Animated.timing(positionAnim, {
        toValue: 0,
        duration: 150,
        useNativeDriver: true,
      }).start();
      Animated.timing(opacityAnim, {
        toValue: 0.5,
        duration: 150,
        useNativeDriver: true,
      }).start();
    } else {
      Animated.timing(positionAnim, {
        toValue: 300,
        duration: 150,
        useNativeDriver: true,
      }).start();
      Animated.timing(opacityAnim, {
        toValue: 0,
        duration: 150,
        useNativeDriver: true,
      }).start();
    }
  }, [props.visible]);

  return useMemo(() => {
    return (
      <>
        <TouchableWithoutFeedback onPress={props.onBackgroundPress}>
          <Animated.View
            style={[styles.backdrop, { opacity: opacityAnim }]}
            pointerEvents={props.visible ? "auto" : "none"}
          />
        </TouchableWithoutFeedback>
        <Animated.View
          style={[
            styles.container,
            {
              transform: [{ translateX: positionAnim }],
              backgroundColor: theme.theme.colors.background,
            },
          ]}
        >
          <SpellMenuContents
            visible={props.visible}
            onSwipeLeft={props.onSwipeLeft}
            onSwipeRight={props.onSwipeRight}
          />
        </Animated.View>
      </>
    );
  }, [
    opacityAnim,
    positionAnim,
    props.visible,
    props.onBackgroundPress,
    theme,
  ]);
}

const styles = StyleSheet.create({
  backdrop: {
    position: "absolute",
    height: "100%",
    width: "100%",
    backgroundColor: "#000",
    zIndex: 80,
  },
  backdropTouchable: {
    position: "absolute",
    height: "100%",
    width: "100%",
    zIndex: 90,
  },
  container: {
    justifyContent: "flex-start",
    alignItems: "stretch",
    position: "absolute",
    zIndex: 100,
    height: "100%",
    width: 300,
    right: 0,
    top: 0,
  },
  magicContainer: {
    padding: 8,
  },
});
