import React, { useMemo } from "react";
import { StyleSheet, TouchableOpacity, View } from "react-native";
import { Badge, useTheme } from "react-native-elements";
import { GameIcon } from "../utility/icons/GameIcon";

export function ItemTile(props: {
  onPress?: () => void;
  icon?: string;
  amount?: number;
  disabled?: boolean;
}) {
  const theme = useTheme();
  const inner = useMemo(
    () => (
      <View style={[styles.tile, { borderColor: theme.theme.colors.grey4 }]}>
        {props.icon && (
          <GameIcon
            style={props.disabled && { opacity: 0.2 }}
            icon={props.icon}
          />
        )}
        {props.amount != null && props.amount > 1 && (
          <Badge
            status="primary"
            value={props.amount}
            containerStyle={{ position: "absolute", top: -8, right: -8 }}
            badgeStyle={{ borderColor: theme.theme.colors.background }}
          />
        )}
      </View>
    ),
    [theme, props.icon, props.amount],
  );
  if (!props.onPress) {
    return inner;
  }
  return (
    <TouchableOpacity onPress={() => props.onPress && props.onPress()}>
      {inner}
    </TouchableOpacity>
  );
}

const styles = StyleSheet.create({
  tile: {
    borderWidth: 0.5,
    height: 48,
    width: 48,
    justifyContent: "center",
    alignItems: "center",
    margin: 8,
  },
});
