import AsyncStorage from "@react-native-async-storage/async-storage";
import * as Clipboard from "expo-clipboard";
import React, {
  RefObject,
  useCallback,
  useContext,
  useRef,
  useState,
} from "react";
import { Platform, StyleSheet, View } from "react-native";
import { CheckBox, Input, useTheme } from "react-native-elements";
import { EventTag } from "../backend/events/GameEvent";
import {
  PlayerContext,
  PlayerContextValue,
  PLAYER_INITIAL_STATE,
} from "../backend/PlayerContext";
import { serializeSaveData } from "../backend/SaveData";
import ConjureBuilding from "../backend/spells/conjuration/ConjureBuilding";
import { saveToFile } from "../utils/FilesystemSave";
import { Button } from "./utility/Button";
import CategoryContainer from "./utility/CategoryContainer";
import LoadGameContainer from "./utility/LoadGameContainer";
import { showExportToFile, showImportFromFile } from "./utility/Miscellaneous";
import { Picker as UtilityPicker } from "./utility/Picker";
import { Text } from "./utility/Text";

const FPS_DATA = ["10", "12", "15", "20", "24", "30", "40", "50", "60"].map(
  (x) => ({ label: x, key: x, value: x }),
);

function useEventTagToggle(
  playerContextRef: RefObject<PlayerContextValue>,
  tag: EventTag,
) {
  return useCallback(() => {
    playerContextRef.current?.apply((oldState) => {
      if (oldState.global.hideEventTags.includes(tag)) {
        oldState.global.hideEventTags.splice(
          oldState.global.hideEventTags.findIndex(
            (innerTag) => innerTag === tag,
          ),
          1,
        );
      } else {
        oldState.global.hideEventTags.push(tag);
      }
      return oldState;
    });
  }, []);
}

export default function Options() {
  const playerContext = useContext(PlayerContext);
  const playerContextRef = useRef(playerContext);
  playerContextRef.current = playerContext;

  const copyToClipboard = useCallback(async () => {
    const serializedData = await serializeSaveData(playerContextRef.current);
    await Clipboard.setStringAsync(serializedData);
  }, []);

  const targetFpsChange = useCallback((value: string) => {
    playerContextRef.current.apply((oldState) => {
      oldState.global.targetFps = Number.parseInt(value);
      return oldState;
    });
  }, []);

  const darkModeToggle = useCallback(() => {
    playerContextRef.current.apply((oldState) => {
      oldState.global.themeMode =
        oldState.global.themeMode === "dark" ? "light" : "dark";
      return oldState;
    });
  }, []);

  const conjureBuildingAutoOffCallback = useCallback(() => {
    playerContextRef.current.apply((oldState) => {
      oldState.global.automaticallyBuiltBuildingsStartOff =
        !oldState.global.automaticallyBuiltBuildingsStartOff;
      return oldState;
    });
  }, []);

  const eventTagLevelUpFifteenMinutesToggle = useEventTagToggle(
    playerContextRef,
    EventTag.LEVEL_UP_FIRST_FIFTEEN_MINS,
  );
  const eventTagLevelUpEmptyToggle = useEventTagToggle(
    playerContextRef,
    EventTag.LEVEL_UP_EMPTY,
  );
  const eventTagLevelUpAllToggle = useEventTagToggle(
    playerContextRef,
    EventTag.LEVEL_UP,
  );
  const eventTagInconsequentialRandomEventsToggle = useEventTagToggle(
    playerContextRef,
    EventTag.INCONSEQUENTIAL_RANDOM_EVENT,
  );
  const eventTagEarlyGameEventsToggle = useEventTagToggle(
    playerContextRef,
    EventTag.EARLY_GAME,
  );

  const [eraseText, setEraseText] = useState("");
  const eraseGame = useCallback(() => {
    playerContextRef.current.apply((state) => {
      return PLAYER_INITIAL_STATE;
    });
    AsyncStorage.removeItem("PlayerContextState");
    AsyncStorage.removeItem("LastSaveSecondsPlayed");
  }, []);

  const theme = useTheme();

  const loadText = showImportFromFile()
    ? "You may import save data by loading it from a file, or by pasting it here."
    : "You may import save data by pasting it here.";

  const saveToFileCallback = useCallback(async () => {
    const result = await saveToFile(playerContextRef.current);
  }, []);

  return (
    <View style={styles.container}>
      <View style={styles.headerContainer}>
        <Text h2>Options</Text>
      </View>
      <CategoryContainer title="Performance">
        <View style={styles.categoryContainer}>
          <Text>
            You may set your target FPS here. Higher FPS will make the game
            smoother but it could affect battery power and CPU usage.
          </Text>
          <View style={styles.targetFpsContainer}>
            <Text>Target FPS:</Text>
            <View style={styles.targetFpsDropdownContainer}>
              <UtilityPicker
                onValueChange={targetFpsChange}
                selectedValue={playerContext.global.targetFps.toString()}
                style={styles.targetFpsPicker}
                data={FPS_DATA}
              />
            </View>
          </View>
        </View>
      </CategoryContainer>
      <CategoryContainer title="Hide Event Popups">
        <View style={styles.categoryContainer}>
          <Text>
            Hide certain kinds of events, so that they don't disrupt gameplay.
            Hidden events will not show a popup, but will still be accessible
            through the Event Menu.
          </Text>
          <View style={styles.eventPopupsContainer}>
            {playerContext.global.totalTimesResetted > 0 && (
              <View>
                <CheckBox
                  title="Hide game progression events that require no action, up to unlocking Exploration"
                  checked={playerContext.global.hideEventTags.includes(
                    EventTag.EARLY_GAME,
                  )}
                  onPress={eventTagEarlyGameEventsToggle}
                  textStyle={styles.checkboxText}
                  containerStyle={styles.checkboxContainer}
                />
              </View>
            )}
            <View>
              <CheckBox
                title="Hide school level up events during the first 15 minutes of a run"
                checked={playerContext.global.hideEventTags.includes(
                  EventTag.LEVEL_UP_FIRST_FIFTEEN_MINS,
                )}
                onPress={eventTagLevelUpFifteenMinutesToggle}
                textStyle={styles.checkboxText}
                containerStyle={styles.checkboxContainer}
              />
            </View>
            <View>
              <CheckBox
                title="Hide school level up events if nothing new is unlocked"
                checked={playerContext.global.hideEventTags.includes(
                  EventTag.LEVEL_UP_EMPTY,
                )}
                onPress={eventTagLevelUpEmptyToggle}
                textStyle={styles.checkboxText}
                containerStyle={styles.checkboxContainer}
              />
            </View>
            <View>
              <CheckBox
                title="Hide all school level up events"
                checked={playerContext.global.hideEventTags.includes(
                  EventTag.LEVEL_UP,
                )}
                onPress={eventTagLevelUpAllToggle}
                textStyle={styles.checkboxText}
                containerStyle={styles.checkboxContainer}
              />
            </View>
            <View>
              <CheckBox
                title="Hide common random events (random events which are not Storylines and which appear more than once)"
                checked={playerContext.global.hideEventTags.includes(
                  EventTag.INCONSEQUENTIAL_RANDOM_EVENT,
                )}
                onPress={eventTagInconsequentialRandomEventsToggle}
                textStyle={styles.checkboxText}
                containerStyle={styles.checkboxContainer}
              />
            </View>
          </View>
        </View>
      </CategoryContainer>
      {ConjureBuilding.isVisible(playerContext) && (
        <CategoryContainer title="Conjure Building">
          <View style={styles.categoryContainer}>
            <Text>
              When the spell Conjure Building is cast automatically, buildings
              that consume resources to operate (such as Furnaces) can be turned
              off by default to prevent extra resource consumption.
            </Text>
            <View style={styles.eventPopupsContainer}>
              <CheckBox
                title="Turn off buildings when automatically created by Conjure Building"
                checked={
                  playerContext.global.automaticallyBuiltBuildingsStartOff
                }
                onPress={conjureBuildingAutoOffCallback}
                textStyle={styles.checkboxText}
                containerStyle={styles.checkboxContainer}
              />
            </View>
          </View>
        </CategoryContainer>
      )}
      <CategoryContainer title="Dark Mode">
        <View style={styles.categoryContainer}>
          <Text>Change the colors to be easier in the eyes.</Text>
          <View style={styles.targetFpsContainer}>
            <CheckBox
              title="Enable dark mode"
              checked={playerContext.global.themeMode === "dark"}
              onPress={darkModeToggle}
              textStyle={styles.checkboxText}
              containerStyle={styles.checkboxContainer}
            />
          </View>
        </View>
      </CategoryContainer>
      <CategoryContainer title="Save File">
        <View style={styles.categoryContainer}>
          <Text>
            You may persist your save data by copying it to the clipboard and
            storing it anywhere you'd like.
          </Text>
          <Button
            onPress={copyToClipboard}
            title="Copy savefile to clipboard"
            containerStyle={styles.saveButton}
          />
        </View>
        {showExportToFile() && (
          <View style={styles.categoryContainer}>
            <Text>
              You can also save your data directly to a file or elsewhere.
            </Text>
            <Button
              onPress={saveToFileCallback}
              title="Save to file"
              containerStyle={styles.saveButton}
            />
          </View>
        )}
      </CategoryContainer>
      <CategoryContainer title="Load File">
        <View style={styles.categoryContainer}>
          <Text>{loadText}</Text>
          <Text style={{ fontWeight: "bold", color: theme.theme.colors.error }}>
            WARNING: This will erase your current save! Make sure you have
            backed up your data!
          </Text>
          <LoadGameContainer />
        </View>
      </CategoryContainer>
      <CategoryContainer title="Erase Saved Data">
        <View style={styles.categoryContainer}>
          <Text>
            If you want to start all over again and retirement isn't enough, you
            can delete your save data entirely.
          </Text>
          <Text style={{ fontWeight: "bold", color: theme.theme.colors.error }}>
            WARNING: This will erase your current save, including all your
            progress with Storylines and retirement! Make sure you have backed
            up your data! Once you have done this, there is no going back! If
            you are sure you want to do this, write "erase" in the text field
            below.
          </Text>
          <Input
            inputContainerStyle={styles.inputContainer}
            value={eraseText}
            onChangeText={setEraseText}
          />
          <Button
            disabled={eraseText.toLowerCase() != "erase"}
            onPress={eraseGame}
            color="error"
            title="Permanently erase all saved data"
          />
        </View>
      </CategoryContainer>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
  headerContainer: {
    marginBottom: 16,
  },
  categoryContainer: {
    marginTop: 8,
    paddingHorizontal: 8,
  },
  targetFpsContainer: {
    flexDirection: "row",
    alignItems: "center",
    marginTop: 8,
  },
  eventPopupsContainer: {
    marginTop: 8,
  },
  targetFpsDropdownContainer: {
    ...Platform.select({
      android: { backgroundColor: "#eee", borderRadius: 10, default: {} },
    }),
    marginLeft: 16,
  },
  targetFpsPicker: {
    width: 100,
  },
  sliderContainer: {
    width: 200,
    marginLeft: 16,
  },
  sliderTextContainer: {
    width: 120,
  },
  checkboxContainer: {
    marginLeft: 0,
    marginTop: 8,
    padding: 0,
  },
  checkboxText: {
    fontWeight: "normal",
  },
  saveButton: {
    marginTop: 8,
  },
  loadMessageContainer: {
    marginBottom: 8,
  },
  inputContainer: {
    borderWidth: 1,
    marginTop: 16,
    maxHeight: 48,
  },
});
