import React, {
  useCallback,
  useContext,
  useMemo,
  useRef,
  useState,
} from "react";
import { Platform, SectionList, StyleSheet, View } from "react-native";
import { Button, CheckBox, useTheme } from "react-native-elements";
import {
  Directions,
  Gesture,
  GestureDetector,
} from "react-native-gesture-handler";
import {
  allocateApprenticesToSpell,
  calculateApprenticeTotal,
  canAutocast,
  getApprenticeAllocation,
  getAutocastSchoolLevelRequirements,
  getEstimatedApprenticeManaConsumptionPerSec,
  getFreeApprentices,
  loadLoadoutFromPosition,
  saveLoadoutToPosition,
} from "../backend/autocast/Autocast";
import { applyTransformations } from "../backend/calculation/Calculation";
import { TransformationTags } from "../backend/calculation/TransformationTags";
import { PlayerContext } from "../backend/PlayerContext";
import { getSchoolLevel, School } from "../backend/spells/ElementsAndSchools";
import { Spell } from "../backend/spells/Spell";
import { getVisibleSpellsBySchool } from "../backend/spells/Spells";
import { calculateIncomePerSecond } from "../backend/timetick/IncomeOverTime";
import { formatNumber } from "../utils/FormattingUtils";
import ActionsContainer from "./utility/ActionsContainer";
import { CategoryHeader } from "./utility/CategoryContainer";
import { Picker as UtilityPicker } from "./utility/Picker";
import { PlusMinusAllocator } from "./utility/PlusMinusAllocator";
import { Text } from "./utility/Text";
import { UIContext, useUIStateFromContext } from "./utility/UIContext";

function SpellRow(props: { spell: Spell; applyAll: boolean }) {
  const spell = props.spell;
  const playerContext = useContext(PlayerContext);
  const playerContextRef = useRef(playerContext);
  playerContextRef.current = playerContext;
  const spellName = spell.getSpellName();
  const apprenticeAllocation = getApprenticeAllocation(playerContext);
  const current = apprenticeAllocation?.[spellName] || 0;
  const secs = applyTransformations(
    [TransformationTags.AutocastDelay],
    playerContext,
    60.0,
  );
  const currentExtra =
    current > 0
      ? `(${formatNumber(secs / current, {
          showDecimals: true,
        })}s/cast)`
      : "";
  const canAllocate = canAutocast(playerContext, spell);
  const freeApprentices = getFreeApprentices(playerContext);
  const hasFreeApprentices = freeApprentices > 0;
  const onPressMinus = useCallback(
    () =>
      playerContextRef.current.apply(
        allocateApprenticesToSpell(spell, props.applyAll ? -Infinity : -1),
      ),
    [spell, props.applyAll],
  );
  const onPressPlus = useCallback(
    () =>
      playerContextRef.current.apply(
        allocateApprenticesToSpell(spell, props.applyAll ? Infinity : 1),
      ),
    [spell, props.applyAll],
  );
  const plusMinusContainer = useMemo(
    () => (
      <View style={styles.plusMinusAllocatorContainer}>
        <PlusMinusAllocator
          disableMinus={current === 0}
          disablePlus={!hasFreeApprentices}
          onPressMinus={onPressMinus}
          onPressPlus={onPressPlus}
        />
      </View>
    ),
    [current, hasFreeApprentices, onPressMinus, onPressPlus],
  );
  const allocatorExtras = useMemo(
    () => (
      <>
        <View style={styles.frequencyTextContainer}>
          <Text>{currentExtra}</Text>
        </View>
        <Text>{current}</Text>
      </>
    ),
    [currentExtra, current],
  );
  const allocator = useMemo(() => {
    let allocatorTemp = (
      <View style={styles.row}>
        {allocatorExtras}
        {plusMinusContainer}
      </View>
    );
    if (!canAllocate) {
      const schoolReqs = getAutocastSchoolLevelRequirements(spell);
      let allReqs = [];
      for (let school in schoolReqs) {
        if (
          getSchoolLevel(playerContext, school as School) <
          schoolReqs[school as School]
        ) {
          allReqs.push(`${school} Lv${schoolReqs[school as School]}`);
        }
      }
      allocatorTemp = (
        <Text style={styles.allocatorText}>Requires: {allReqs.join(", ")}</Text>
      );
    }
    return allocatorTemp;
  }, [allocatorExtras, canAllocate, spell, plusMinusContainer]);

  const nameContainer = useMemo(
    () => (
      <View style={styles.spellNameContainer}>
        <Text numberOfLines={3}>{spellName}:</Text>
      </View>
    ),
    [spellName],
  );

  return useMemo(
    () => (
      <View style={[styles.row, styles.spellRowContainer]}>
        {nameContainer}
        {allocator}
      </View>
    ),
    [spellName, allocator],
  );
}

