const VIEWABILITY_CONFIG = {
  minimumViewTime: 0,
  itemVisiblePercentThreshold: 1,
  waitForInteraction: false,
};

function emptySpaceHeader() {
  return <View style={styles.emptySpaceHeader} />;
}

export function Autocast(props: {
  onSwipeLeft: () => void;
  onSwipeRight: () => void;
}) {
  const playerContext = useContext(PlayerContext);
  const playerContextRef = useRef(playerContext);
  playerContextRef.current = playerContext;

  const loadoutAmount = 4;
  const [slotNumber, setSlotNumber] = useState(
    playerContext?.autocast?.lastLoadoutUsed?.toString() ?? "0",
  );

  const spellsBySchool = getVisibleSpellsBySchool(playerContext);
  const freeApprentices = getFreeApprentices(playerContext);

  const toggleApprenticesPaused = useCallback(() => {
    playerContextRef.current.apply((state) => {
      state.autocast.apprenticesPaused = !state.autocast.apprenticesPaused;
      return state;
    });
  }, []);

  const loadoutsSlotPicker = useMemo(() => {
    const items = [];
    for (let i = 0; i < loadoutAmount; i++) {
      items.push({
        key: i.toString(),
        value: i.toString(),
        label: `Slot ${i + 1}`,
      });
    }
    return (
      <UtilityPicker
        onValueChange={setSlotNumber}
        selectedValue={slotNumber}
        data={items}
      />
    );
  }, [loadoutAmount, setSlotNumber, slotNumber]);

  const totalCost = getEstimatedApprenticeManaConsumptionPerSec(playerContext);
  const manaIncome = calculateIncomePerSecond(playerContext)?.Mana ?? 0;
  const totalApprentices = calculateApprenticeTotal(playerContext);
  const theme = useTheme();

  const uiContext = useContext(UIContext);

  const [applyAll, setApplyAll] = useUIStateFromContext(
    uiContext,
    "autocast_applyall",
    false,
  );

  const toggleApplyAll = useCallback(() => {
    setApplyAll(!applyAll);
  }, [applyAll, setApplyAll]);

  const apprenticeData = useMemo(
    () => (
      <View
        style={[
          styles.apprenticeDataContainer,
          { borderTopColor: theme.theme.colors.divider },
        ]}
      >
        <View style={styles.assignAllContainer}>
          <CheckBox
            title="Assign / unassign all at once"
            checked={applyAll}
            textStyle={styles.checkboxText}
            containerStyle={styles.checkboxContainer}
            onPress={toggleApplyAll}
          />
        </View>
        <View style={[styles.row, styles.totalApprenticesContainer]}>
          <Text>Apprentices Available: </Text>
          <Text>
            {formatNumber(freeApprentices)}/{formatNumber(totalApprentices)}
          </Text>
        </View>
        <View style={[styles.row, styles.manaCostContainer]}>
          <Text>Estimated Mana consumption: </Text>
          <Text>{formatNumber(totalCost, { showDecimals: true })}/sec</Text>
        </View>
        <View style={[styles.apprenticeWarningContainer]}>
          {totalCost > manaIncome && (
            <Text style={{ color: theme.theme.colors.warning }}>
              Warning: Your Mana income may not be enough to sustain the
              Apprentices!
            </Text>
          )}
        </View>
      </View>
    ),
    [
      totalCost,
      manaIncome,
      freeApprentices,
      totalApprentices,
      toggleApplyAll,
      applyAll,
      theme,
    ],
  );

  const footer = useMemo(
    () => (
      <View>
        <View style={[styles.marginBottom, styles.marginTop]}>
          {playerContext.autocast.apprenticesPaused ? (
            <Button
              title="Unpause Apprentices"
              onPress={toggleApprenticesPaused}
            />
          ) : (
            <Button
              title="Pause Apprentices"
              onPress={toggleApprenticesPaused}
            />
          )}
        </View>
        <View style={[styles.loadoutsRow, styles.marginBottom]}>
          <Text>Loadouts:</Text>
          <View style={styles.loadoutRest}>
            <View style={styles.pickerContainer}>{loadoutsSlotPicker}</View>
            <Button
              containerStyle={styles.loadoutButtons}
              title="Save"
              onPress={() => {
                saveLoadoutToPosition(
                  playerContextRef.current,
                  Number.parseInt(slotNumber),
                );
              }}
            />
            <Button
              containerStyle={styles.loadoutButtons}
              title="Load"
              onPress={() => {
                loadLoadoutFromPosition(
                  playerContextRef.current,
                  Number.parseInt(slotNumber),
                );
              }}
            />
          </View>
        </View>
        <ActionsContainer area="Autocast" />
      </View>
    ),
    [
      slotNumber,
      loadoutsSlotPicker,
      playerContext.autocast.apprenticesPaused,
      toggleApprenticesPaused,
      freeApprentices,
      totalApprentices,
      totalCost,
      manaIncome,
      applyAll,
      toggleApplyAll,
    ],
  );

  const spellsBySchoolJSON = JSON.stringify(spellsBySchool);
  const listData = useMemo(
    () =>
      Object.entries(spellsBySchool).map((item) => ({
        title: item[0],
        data: item[1],
      })),
    [spellsBySchoolJSON],
  );
  const renderItem = useCallback(
    (item: { item: Spell }) => (
      <SpellRow
        key={item.item.getName()}
        spell={item.item}
        applyAll={applyAll}
      />
    ),
    [applyAll],
  );
  const keyExtractor = useCallback((item: Spell) => item?.getName?.(), []);
  const renderSectionHeader = useCallback(
    (info: { section: { title: string; data: Spell[] } }) => (
      <CategoryHeader
        title={info.section.title}
        containerStyle={{ backgroundColor: theme.theme.colors.background }}
      />
    ),
    [theme],
  );
  const sectionList = useMemo(
    () => (
      <SectionList
        sections={listData}
        initialNumToRender={30}
        renderItem={renderItem}
        keyExtractor={keyExtractor}
        renderSectionHeader={renderSectionHeader}
        ListFooterComponent={footer}
        style={styles.sectionListStyle}
        contentContainerStyle={styles.sectionListInnerStyle}
      />
    ),
    [listData, renderItem, keyExtractor, renderSectionHeader, footer],
  );

  const rightGesture = useMemo(
    () =>
      Gesture.Fling()
        .direction(Directions.RIGHT)
        .onEnd((e: any, success) => {
          if (success && (Platform.OS != "web" || e.translationX > 0)) {
            props.onSwipeRight();
          }
        }),
    [props.onSwipeRight],
  );
  const leftGesture = useMemo(
    () =>
      Gesture.Fling()
        .direction(Directions.LEFT)
        .onEnd((e: any, success) => {
          if (success && (Platform.OS != "web" || e.translationX < 0)) {
            props.onSwipeLeft();
          }
        }),
    [props.onSwipeLeft],
  );
  const nativeGesture = useMemo(() => Gesture.Native(), []);
  const compositeGesture = useMemo(
    () => Gesture.Simultaneous(rightGesture, leftGesture, nativeGesture),
    [rightGesture, leftGesture, nativeGesture],
  );

  const rightGestureBottom = useMemo(
    () =>
      Gesture.Fling()
        .direction(Directions.RIGHT)
        .onEnd((e: any, success) => {
          if (success && (Platform.OS != "web" || e.translationX > 0)) {
            props.onSwipeRight();
          }
        }),
    [props.onSwipeRight],
  );
  const leftGestureBottom = useMemo(
    () =>
      Gesture.Fling()
        .direction(Directions.LEFT)
        .onEnd((e: any, success) => {
          if (success && (Platform.OS != "web" || e.translationX < 0)) {
            props.onSwipeLeft();
          }
        }),
    [props.onSwipeLeft],
  );
  const nativeGestureBottom = useMemo(() => Gesture.Native(), []);
  const compositeGestureBottom = useMemo(
    () =>
      Gesture.Simultaneous(
        rightGestureBottom,
        leftGestureBottom,
        nativeGestureBottom,
      ),
    [rightGestureBottom, leftGestureBottom, nativeGestureBottom],
  );

  const contents = useMemo(
    () => (
      <View style={styles.fullContainer}>
        <GestureDetector key="autocastGesture" gesture={compositeGesture}>
          {sectionList}
        </GestureDetector>
        <GestureDetector key="dataGesture" gesture={compositeGestureBottom}>
          {apprenticeData}
        </GestureDetector>
      </View>
    ),
    [sectionList, apprenticeData, compositeGesture, compositeGestureBottom],
  );

  return contents;
}

