import React, { useMemo } from "react";
import { ImageSourcePropType, StyleSheet, View } from "react-native";
import { Image, useTheme } from "react-native-elements";
import { sanitizeImageSourceForElectron } from "../utility/icons/IconUtils";

export function SummonSpriteTile(props: { sprite?: ImageSourcePropType }) {
  const theme = useTheme();
  return useMemo(
    () => (
      <View style={[styles.tile, { borderColor: theme.theme.colors.grey5 }]}>
        {props.sprite && (
          <Image
            resizeMode="contain"
            source={sanitizeImageSourceForElectron(props.sprite)}
            style={styles.image}
          />
        )}
      </View>
    ),
    [props.sprite, theme],
  );
}

const styles = StyleSheet.create({
  tile: {
    borderWidth: 1,
    height: 48,
    width: 48,
    justifyContent: "center",
    alignItems: "stretch",
  },
  image: {
    height: 46,
    width: 46,
  },
});
