import { DataStore } from "../generic/DataStore";
import { Identifiable } from "../generic/Identifiable";
import { PlayerContextTransform } from "../PlayerContext";

export type WarpTimeTransform = (
  secsPassed: number,
) => PlayerContextTransform | null | undefined | void;

export class WarpTimeListener implements Identifiable {
  id: string;
  listener: WarpTimeTransform;

  constructor(id: string, listener: WarpTimeTransform) {
    this.id = id;
    this.listener = listener;
  }

  getId(): string {
    return this.id;
  }

  getListener(): WarpTimeTransform {
    return this.listener;
  }
}

const WarpTimeListeners = new DataStore<WarpTimeListener>();

export { WarpTimeListeners };
